@keyframes blink {
  0%{
    opacity: 0
  }
  25%{
    opacity: 1
  }
  50%{
    opacity: 0
  }
  75%{
    opacity: 1
  }
  100%{
    opacity: 0
  }
}

@keyframes fadeIn{
  0%{
    opacity: 0
  }
  100%{
    opacity: 1
  }
}

@keyframes flicker {
  0%{
    opacity:0
  }
  5%{
    opacity:1
  }
  15%{
    opacity:0
  }
  20%{
    opacity:1
  }
  25%{
    opacity:0
  }
  30%{
    opacity:1
  }
  50%{
    opacity:0
  }
  100%{
    opacity:1
  }
}
